import React, { useEffect } from 'react';
import { ControlProps, useMap } from '../Hooks';
import 'semantic-ui-css/semantic.min.css';
import { Options as TileDebugOptions } from 'ol/source/TileDebug';
import StyledMenu, { StyledMenuProps } from './StyledMenu';
import styles from './styles.module.scss';

export interface StyledMapProps {
  id?: string;
  osmBasemap?: boolean;
  defaultControls?: ControlProps;
  controlsMenu?: StyledMenuProps;
  startZoom?: number;
  startCoordinates?: [number, number];
  tileDebug?: TileDebugOptions;
}

const StyledMap: React.FC<StyledMapProps> & {
  Controls: React.FC<StyledMenuProps>;
} = ({
  id,
  osmBasemap,
  defaultControls,
  controlsMenu,
  startZoom,
  startCoordinates,
  tileDebug,
  children,
}) => {
  const { initMap } = useMap();
  useEffect(() => {
    initMap({
      id: id || 'map',
      startCoordinates: startCoordinates || [0, 0],
      startZoom: startZoom,
      defaultControls,
      defaultOSMBasemap: osmBasemap,
      tileDebug,
    });
  }, [
    id,
    osmBasemap,
    defaultControls,
    initMap,
    startCoordinates,
    startZoom,
    tileDebug,
  ]);
  return (
    <div className={styles.container}>
      <div
        id={id || 'map'}
        style={{
          width: '100%',
          height: '100%',
        }}
      />
      {controlsMenu && <StyledMenu {...controlsMenu} />}
      {children}
    </div>
  );
};

StyledMap.Controls = StyledMenu;

export default StyledMap;
